import React from 'react';
import T from 'prop-types';
import styled from 'styled-components';
import {
    StatusOk,
    StatusInfo,
    StatusWarning,
    StatusError,
} from '@splunk/dashboard-icons';
import {
    formatRiskyCommandErrorMessage,
    isRiskyCommandError,
} from '@splunk/dashboard-utils/riskyCommandUtils';
import Clickable from '@splunk/react-ui/Clickable';
import { variables } from '@splunk/themes';

import OptionalTooltip from './utils/OptionalTooltip';
import RiskyWarningPopover from './RiskyWarningPopover';

const StatusIconContainer = styled.div`
    height: ${(props) => props.height}px;
    width: ${(props) => props.width}px;
    // padding to increase the hover area on the icon to show tooltip
    padding: 0 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
        height: ${(props) => props.iconSize}px;
        width: ${(props) => props.iconSize}px;
    }

    // Increase SVG size when this component is hovered over
    &:hover svg {
        height: ${(props) => props.iconSizeOnHover}px;
        width: ${(props) => props.iconSizeOnHover}px;
    }
`;

const StatusIconWrapper = styled(Clickable)`
    // the child svg icon has an attribute 'currentColor' that will inherit this 'color' property.
    color: ${(props) => variables[props.colorKey]};
    cursor: ${(props) => (props.showCursor ? 'pointer' : 'auto')};
`;

/**
 * map that containts:
 * [status]: {
 *     component: React.element
 *     statusColorKey: string
 * }
 */
const statusMap = {
    error: {
        component: StatusError,
        statusColorKey: 'statusColorHigh',
    },
    info: { component: StatusInfo, statusColorKey: 'statusColorMedium' },
    ok: { component: StatusOk, statusColorKey: 'statusColorInfo' },
    warning: {
        component: StatusWarning,
        statusColorKey: 'statusColorMedium',
    },
};

const StatusIconPlaceholder = styled.div`
    width: ${(props) => props.width + 24}px;
`;

const handleMouseDown = (e) => {
    e.stopPropagation();
};

/**
 * Component to display the status of a Splunk Search.
 */
const SearchStatusIcon = ({
    height,
    message,
    status,
    search,
    id,
    iconSize,
    iconSizeOnHover,
}) => {
    if (!status) {
        /**
         * Intentionally have status as an optional parameter as we do not want to render an icon in either of the scenarios:
         * - partial search data has been returned
         * - total search data has been returned
         */
        return <StatusIconPlaceholder width={iconSizeOnHover} />;
    }

    const StatusIcon = statusMap[status].component;
    const colorKey = statusMap[status].statusColorKey;

    const isRiskyCommand = isRiskyCommandError(message);

    const tooltipMessage = isRiskyCommand
        ? formatRiskyCommandErrorMessage(message)
        : message;

    const searchStatusIcon = (
        <StatusIconWrapper
            colorKey={colorKey}
            showCursor={isRiskyCommand}
            tabIndex={isRiskyCommand ? 0 : -1}
        >
            <StatusIcon hideDefaultTooltip />
        </StatusIconWrapper>
    );

    return (
        <OptionalTooltip message={tooltipMessage}>
            <StatusIconContainer
                height={height}
                width={iconSizeOnHover}
                iconSize={iconSize}
                iconSizeOnHover={iconSizeOnHover}
                onMouseDown={isRiskyCommand ? handleMouseDown : undefined}
                data-test="search-status-icon-container"
            >
                {isRiskyCommand ? (
                    <RiskyWarningPopover
                        statusIcon={searchStatusIcon}
                        riskySearch={search}
                        id={id}
                        message={message}
                    />
                ) : (
                    searchStatusIcon
                )}
            </StatusIconContainer>
        </OptionalTooltip>
    );
};

SearchStatusIcon.propTypes = {
    /**
     * Splunk search status. This value maps to a respective status icon.
     */
    status: T.oneOf(Object.keys(statusMap)),
    /**
     * Message that will be displayed on hover of the status icon.
     */
    message: T.string,
    /**
     * Base icon size.
     */
    iconSize: T.number,
    /**
     * Icon size when hovered upon.
     */
    iconSizeOnHover: T.number,
    /**
     * Height of container surrounding icon
     */
    height: T.number,
    /**
     *  Viz Id
     */
    id: T.string,
    /**
     * Search Query
     */
    search: T.string,
};

SearchStatusIcon.defaultProps = {
    /**
     * Base icon size.
     */
    iconSize: 10,
    /**
     * Icon size when hovered upon.
     */
    iconSizeOnHover: 12,
};

export default React.memo(SearchStatusIcon);
