import React, {
    useState,
    useCallback,
    useMemo,
    useContext,
    useEffect,
} from 'react';
import T from 'prop-types';
import styled from 'styled-components';
import { _ } from '@splunk/ui-utils/i18n';
import Input from '@splunk/react-search/components/Input';
import Dropdown from '@splunk/react-ui/Dropdown';
import Button from '@splunk/react-ui/Button';
import Close from '@splunk/react-icons/Close';
import Link from '@splunk/react-ui/Link';
import Scroll from '@splunk/react-ui/Scroll';
import searchBNF from '@splunk/dashboard-utils/defaultSPLSyntax.json';
import DashboardApiContext from '@splunk/dashboard-context/DashboardApiContext';
import { getRiskyCommands } from '@splunk/dashboard-utils/riskyCommandUtils';
import DashboardContext from '@splunk/dashboard-context';
import { createDocsURL } from '@splunk/splunk-utils/url';
import get from 'lodash/get';
import { variables, pick } from '@splunk/themes';
import { TelemetryContext } from '@splunk/dashboard-telemetry';

const WarningContainer = styled.div`
    width: 400px;
    max-height: 375px;
    padding: 24px;
    color: ${variables.contentColorDefault};
    font-size: 14px;
    background-color: ${variables.backgroundColorPopup};
`;

const BodyContainer = styled(Scroll)`
    width: 100%;
    max-height: 280px;
    margin-bottom: 12px;
`;

const HeaderContainer = styled.div`
    display: flex;
    justify-content: space-between;
`;

const TitleContainer = styled.div`
    height: 28px;
    font-size: 17px;
    font-weight: 500;
`;

const CloseButton = styled(Button)`
    width: 32px;
    height: 32px;
    flex-grow: 0;
    margin-top: -8px;
`;

const FooterContainer = styled.div`
    text-align: right;
    margin-top: 16px;
`;

const InputContainer = styled.div`
    .disabled .ace_content {
        opacity: 1;
    }
`;

const RiskyCommandsList = styled.div`
    font-weight: 500;
    color: ${pick({
        light: variables.focusColor,
        dark: variables.linkColor,
    })};
    text-indent: 24px;
    word-break: break-word;
`;

const DescriptionText = styled.div.attrs(() => ({
    'data-test': 'risky-description-text',
}))`
    padding-top: 16px;
    padding-bottom: 8px;
`;

const WarningText = styled.div`
    font-weight: 500;
    margin-top: 8px;
    margin-bottom: 16px;
`;

const closeReasons = ['escapeKey', 'offScreen', 'toggleClick', 'clickAway'];

const RiskyWarningPopover = ({
    riskySearch,
    message,
    id,
    statusIcon,
    open = false,
    type = 'visualization',
}) => {
    const [status, setPopoverStatus] = useState(open);
    const dashboardApi = useContext(DashboardApiContext);
    const telemetry = useContext(TelemetryContext);

    const handleClick = useCallback(() => {
        telemetry.emit({
            pageAction: 'riskyWarningDialog.runSearch',
            source: 'riskyWarningDialog',
            metadata: { type },
        });
        if (type === 'input') {
            dashboardApi.refreshInput(id, { checkRiskyCommand: false });
        } else {
            dashboardApi.refreshVisualization(id, { checkRiskyCommand: false });
        }
    }, [dashboardApi, id, type, telemetry]);

    const handleRequestClose = useCallback(() => {
        telemetry.emit({
            pageAction: 'riskyWarningDialog.cancel',
            source: 'riskyWarningDialog',
            metadata: { type },
        });
        setPopoverStatus(false);
    }, [telemetry, type]);

    const handleRequestOpen = useCallback(() => {
        telemetry.emit({
            pageAction: 'riskyWarningDialog.open',
            source: 'riskyWarningDialog',
            metadata: { type },
        });
        setPopoverStatus(true);
    }, [telemetry, type]);

    const { userMessage } = useContext(DashboardContext);

    useEffect(() => {
        if (userMessage) {
            userMessage();
        }
    }, [userMessage]);

    const renderRiskyCommands = useMemo(
        () => (
            <RiskyCommandsList data-test="risky-command-list">
                {getRiskyCommands(message).join(', ')}
            </RiskyCommandsList>
        ),
        [message]
    );

    const getRiskyCommandDocsUrl = () => {
        const instanceType = get(window, [
            '__splunkd_partials__',
            `/services/server/info`,
            'entry',
            0,
            'content',
            'instance_type',
        ]);

        return instanceType === 'cloud'
            ? createDocsURL('sds.cloud-risky')
            : createDocsURL('sds.ent-risky');
    };

    const WarningDialog = useMemo(
        () => (
            <WarningContainer>
                <HeaderContainer>
                    <TitleContainer>
                        {type === 'input'
                            ? _('This input has a potential security risk')
                            : _(
                                  'This visualization has a potential security risk'
                              )}
                    </TitleContainer>
                    <CloseButton
                        appearance="pill"
                        prepend
                        icon={<Close />}
                        onClick={handleRequestClose}
                        data-test="risky-warning-close-button"
                    />
                </HeaderContainer>
                <BodyContainer stopScrollPropagation>
                    <DescriptionText>
                        {_(
                            'The search that you are about to run contains commands that might present a security risk.'
                        )}{' '}
                        <Link to={getRiskyCommandDocsUrl()} openInNewContext>
                            {_('Learn More')}
                        </Link>
                    </DescriptionText>
                    {renderRiskyCommands}
                    <WarningText>
                        {_(
                            'If the commands seem unusual, do not run the query without further investigation or contact your Splunk administrator.'
                        )}
                    </WarningText>
                    <InputContainer>
                        <Input
                            syntax={searchBNF}
                            showLineNumbers={false}
                            maxLines={4}
                            value={riskySearch}
                            enabled={false}
                        />
                    </InputContainer>
                </BodyContainer>
                {_('Do you want to run the search string?')}
                <FooterContainer>
                    <Button
                        label={_('Run Query')}
                        appearance="primary"
                        onClick={handleClick}
                        data-test="risky-warning-run-button"
                    />
                    <Button
                        label={_('Cancel')}
                        appearance="secondary"
                        onClick={handleRequestClose}
                        data-test="risky-warning-cancel-button"
                    />
                </FooterContainer>
            </WarningContainer>
        ),
        [
            handleRequestClose,
            renderRiskyCommands,
            riskySearch,
            handleClick,
            type,
        ]
    );

    return useMemo(
        () => (
            <Dropdown
                defaultPlacement="right"
                open={status}
                closeReasons={closeReasons}
                toggle={statusIcon}
                onRequestOpen={handleRequestOpen}
                onRequestClose={handleRequestClose}
                takeFocus
                retainFocus
            >
                {WarningDialog}
            </Dropdown>
        ),
        [
            handleRequestClose,
            handleRequestOpen,
            status,
            statusIcon,
            WarningDialog,
        ]
    );
};

RiskyWarningPopover.propTypes = {
    statusIcon: T.node.isRequired,
    riskySearch: T.string.isRequired,
    id: T.string.isRequired,
    message: T.string.isRequired,
    open: T.bool,
    type: T.oneOf(['visualization', 'input']),
};

export default React.memo(RiskyWarningPopover);
